import React, { Component } from 'react';
import styled, { ThemeProvider } from 'styled-components';
import Grid from 'styled-components-grid';
import block1 from '../static/block1.png';
import block2 from '../static/block2.png';
import block3 from '../static/block3.png';
import block4 from '../static/block4.png';
import block5 from '../static/block5.png';
import block6 from '../static/block6.png';

const Banner = styled.div`
    width: 100%;
    background-color: #fff;
`
const Wrapper = styled.div`
    width: 85%;
    margin: 0px auto;
`
const theme = {
    breakpoints: {
        xs: 0,
        sm: 576,
        md: 768,
        lg: 992,
        xl: 1200
    }
}
const Card = styled.div`
    padding: 6%;
`
const Header = styled.div`
    text-align: center;
`
const Context = styled.div`
    padding: 3%;
    color: #999;
    line-height: 1.8em;
`
const Title = styled.div`
    color: #555;
    padding: 3%;
    font-weight: bold;
`
const Img = styled.img`
    
`

export default class Center extends Component {
    render (){
        return (
            <Banner>
                <Wrapper>
                    <ThemeProvider theme={theme}>
                        <Grid>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>
                                    
                                    <Header>
                                        <Img src={block1}/>
                                        <Title>科研自动化</Title>
                                    </Header>
                                    <Context>
                                        epihero强大的自动化功能可以轻松协助您开展大型公卫科研及其他科研数据录入，进行前瞻性或横断面的大样本量研究，并自动对研究对象进行分组并可进行后续的自动短信追踪随访，协助您开展公共卫生领域最具雄心的调研项目，让您将宝贵的精力与经费分配给其他更具挑战性的工作。
                                    </Context>

                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>
                                    <Header>
                                        <Img src={block2}/>
                                        <Title>大大降低科研成本</Title>
                                    </Header>
                                    <Context>
                                        epihero采用透明的按年取使用费的模式，大大节约科研经费以及人力成本，较传统的建数据库及定制化软件或纸质问卷模式为科研人员节约超过90%成本，没有隐藏收费，没有额外的维护及升级费用，这只有专注于服务医疗领域的epihero才能做到。
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block3}/>
                                        <Title>完善的安全合规方案</Title>
                                    </Header>
                                    <Context>
                                        epihero采用业内最高的安全标准，多层加密，严防数据外泄，并与客户签订业内最高标准的数据保密协议，将责任落实到人，得到众多顶级国际级项目的信赖。
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block4}/>
                                        <Title>全面的解决方案，完善的售后服务</Title>
                                    </Header>
                                    <Context>
                                        epihero的原则是绝不将产品推销给不适合的客户，我们的团队可以配合您评估您的需求，并为您提供完整的培训及售后服务，确保您购买epihero前对其有完整的了解，购买后也能顺畅上手。
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block5}/>
                                        <Title>泛用且轻松扩展</Title>
                                    </Header>
                                    <Context>
                                        epihero作为一个灵活的医疗数据平台，无论是临床医疗数据还是公卫科研数据都能收集，客户无需懂得任何编程技术既可自行轻松通过后台自定义开展多种多样的数据采集项目，以满足不同客户灵活多变的需求。
                                    </Context>
                                </Card>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, sm: 1/2, md: 1/3} }>
                                <Card>

                                    <Header>
                                        <Img src={block6}/>
                                        <Title>经验证</Title>
                                    </Header>
                                    <Context>
                                        每个新功能的添加与每次产品升级都从公卫人员实际需求入手，已有多家权威医疗机构以及大型国家级数据采集项目选择与epihero合作。
                                    </Context>
                                </Card>
                            </Grid.Unit>
                        </Grid>
                    </ThemeProvider>
                </Wrapper>
            </Banner>
        )
    }
}



