import React, { Component } from 'react';
import banner from '../static/banner.jpg';
import styled from 'styled-components';

const Wrapper = styled.div`
    width: 100%;
    height: auto;
    position: fixed;
    z-index: -10;
    top: 0px;
`
const Img = styled.img`
    width: 100%;
    height: auto;
`

export default class Screen extends Component {
    render (){
        return (
            <Wrapper>
                <Img src={banner} />
            </Wrapper>
        )
    }
}


