import React, { Component } from 'react';
import styled, { ThemeProvider } from 'styled-components';
import Grid from 'styled-components-grid';
import wechat from '../static/wechat.jpg';

const Banner = styled.div`
    background-color: #333;
`
const Wrapper = styled.div`
    width: 85%;
    margin: 0 auto;
    padding: 8px;
`
const theme = {
    breakpoints: {
        xs: 0,
        sm: 576,
        md: 768,
        lg: 992,
        xl: 1200
    }
}
const Context = styled.div`
    color: #999;
    height: 120px;
    align-items:center;
    display: flex;
`
const Img = styled.img`
`
const Card = styled.div`
    
`
const Header = styled.div`
    height: 100px;
    text-align: center;
`
const Card_Footer = styled.div`
    text-align: center;
    color: #fff;
    font-size: 0.9em;
    text-transform: uppercase;
    letter-spacing: 1px;
`

export default class Footer extends Component {
    render (){
        return (
            <Banner>
                <Wrapper>
                    <ThemeProvider theme={theme}>
                        <Grid>
                            <Grid.Unit size={ {xs: 1/1, md: 5/6} }>
                                <Context>
                                    <span>© 2016-2019 广州龙蟠网络科技有限公司 版权所有 ICP证: 粤ICP备15063328号</span>
                                </Context>
                            </Grid.Unit>
                            <Grid.Unit size={ {xs: 1/1, md: 1/6} }>
                                <Card>
                                    <Header>
                                        <Img src={wechat} />
                                    </Header>
                                    <Card_Footer>关注官方微信</Card_Footer>
                                </Card>
                            </Grid.Unit>
                        </Grid>
                    </ThemeProvider>
                </Wrapper>
            </Banner>
        )
    }
}


