import React, { Component } from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    width: 85%;
    margin: 0px auto;
    padding-bottom: 35px;
`
const Logo = styled.div`
    font-size: 36px;
    color: #fff;
    height: 50px;
    padding: 12px 0 0 0;
`
const Title = styled.div`
    font-size: 36pt;
    font-weight: bold;
    color: #fff;
    text-align: center;
    padding-bottom: 12px;
`
const Subtitle = styled.div`
    font-size: 16px;
    color: #fff;
    text-align: center;
    padding-bottom: 160px;
`

export default class Header extends Component {
    render() {
        return (
            <Wrapper>
                <Logo>epihero</Logo>
                <Title>自动化医疗数据管理平台</Title>
                <Subtitle>问卷调研 | 横断面研究 | 前瞻性纵向研究 | 人群健康管理与随访 | 数据上报网络</Subtitle>
            </Wrapper>
        )
    }
}


